<template>
  <md-app md-waterfall md-mode="fixed">
    <md-app-toolbar class="md-large md-dense md-primary">
      <div class="md-toolbar-row">
        <div class="md-toolbar-section-start">
          <md-button class="md-icon-button" @click="menuVisible = !menuVisible">
            <md-icon>menu</md-icon>
          </md-button>

          <span class="md-title">My Title</span>
        </div>

        <div class="md-toolbar-section-end">
          <md-button class="md-icon-button">
            <md-icon>more_vert</md-icon>
          </md-button>
        </div>
      </div>

      <div class="md-toolbar-row">
        <md-tabs class="md-primary">
          <md-tab id="tab-all" md-label="Tudo"></md-tab>
          <md-tab id="tab-despesas" md-label="Despesas"></md-tab>
          <md-tab id="tab-receitas" md-label="Receitas"></md-tab>
        </md-tabs>
      </div>
    </md-app-toolbar>

    <md-app-drawer :md-active.sync="menuVisible">
      <md-toolbar class="md-transparent" md-elevation="0">Navigation</md-toolbar>

      <md-list>
        <md-list-item>
          <md-icon>move_to_inbox</md-icon>
          <span class="md-list-item-text">Inbox</span>
        </md-list-item>

        <md-list-item>
          <md-icon>send</md-icon>
          <span class="md-list-item-text">Sent Mail</span>
        </md-list-item>

        <md-list-item>
          <md-icon>delete</md-icon>
          <span class="md-list-item-text">Trash</span>
        </md-list-item>

        <md-list-item>
          <md-icon>error</md-icon>
          <span class="md-list-item-text">Spam</span>
        </md-list-item>
      </md-list>
    </md-app-drawer>

    <md-app-content>
      <md-table v-model="allItems" md-card @md-selected="onSelect" md-sort="due" md-sort-order="asc">
        <md-table-toolbar>
          <h3 class="md-title" style="flex: 1">TOTAL {{ total }}</h3>
        </md-table-toolbar>

        <!-- <md-table-toolbar slot="md-table-alternate-header" slot-scope="{ count }">
          <div class="md-toolbar-section-start">{{ getAlternateLabel(count) }}</div>

          <div class="md-toolbar-section-end">
            <md-button class="md-icon-button">
              <md-icon>delete</md-icon>
            </md-button>
          </div>
        </md-table-toolbar> -->

        <md-table-row slot="md-table-row" slot-scope="{ item }" md-selectable="multiple" md-auto-select>
          <md-table-cell md-label="Item" md-sort-by="description">{{ item.description }}</md-table-cell>
          <md-table-cell md-label="Data" md-sort-by="due">{{ item.due | moment('L') }}</md-table-cell>
          <md-table-cell md-label="Value" md-sort-by="value">{{ item.value }}</md-table-cell>
        </md-table-row>
      </md-table>
    </md-app-content>
  </md-app>
</template>

<!-- <script async defer src="https://apis.google.com/js/api.js"></script> -->

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'bills-view',
  data () {
    return {
      // items: {},
      selected: [],
      loading: false,
      error: null,
      receitas: true,
      despesas: true,
      menuVisible: false
    }
  },
  mounted: function () {
    // this.items = getBills(this.fileContent)
    // GapiIntegration.loadDriveApis()
    //   .then(() => {
    //     console.log('starting authorize')
    //     GapiIntegration.authorize(true, this.user)
    //       .then(() => {
    //         return this.loadThisFile()
    //       })
    //       .catch((reason) => {
    //         console.log('error inload or authorize')
    //         this.$refs.login.open()
    //       })
    //   })
  },
  created () {
    console.log(this.items)
    this.byJovane()
    // this.fetchData()
  },
  methods: {
    byJovane () {
      console.log('desenvolvido por @jovanepires')
    },
    changeFilter: function (tabIndex) {
      console.log(this.items)
      if (tabIndex === 0) {
        this.receitas = true
        this.despesas = true
      }
      if (tabIndex === 1) {
        this.receitas = false
        this.despesas = true
      }
      if (tabIndex === 2) {
        this.receitas = true
        this.despesas = false
      }
    },
    onSelect (items) {
      this.selected = items
    },
    getAlternateLabel (count) {
      let plural = ''

      if (count > 1) {
        plural = 's'
      }

      return `TOTAL ${this.total} for ${count} item${plural} selected`
    },
    ...mapActions([
      'editContent'
    ])
  },
  computed: {
    ...mapState({
      items: state => state.bills.items
    }),
    allItems: function () {
      // this.items = getBills(this.fileContent || '{}')
      if (this.items) {
        return this.items.slice(0, 20)
      }

      return []
    },
    negativeItems: function () {
      if (!this.items) {
        return {}
      }
      return this.items.filter(function (item) {
        return item.value < 0
      })
    },
    positiveItems: function () {
      if (!this.items) {
        return {}
      }
      return this.items.filter(function (item) {
        return item.value > 0
      })
    },
    total: function () {
      if (!this.items) {
        return 0
      }
      let _self = this
      let _items = this.selected || this.items
      return _items.filter(function (item) {
        if (_self.receitas && _self.despesas) {
          return true
        }
        if (_self.receitas) {
          return item.value > 0
        }
        if (_self.despesas) {
          return item.value < 0
        }
        return false
      }).reduce(function (a, i) {
        return a + i.value
      }, 0)
    },
    activeClass: function () {
      return this.total >= 0 ? 'green' : 'red'
    }
  }
}
</script>
<style>
  /*body { padding-bottom: 70px; }*/

  table {
    background: #ffffff;
  }
  .bills header {
    text-align: center;
    color: #fff;
    /*border-bottom: 1px solid #ededed;*/
  }
  .bills header.positive {
    background-color: #1abc9c !important;
  }
  .bills header.negative {
    background-color: #e74c3c !important;
  }
  .header-title {
    outline: none;
    position: relative;
    margin: 0 auto;
  }
  .header-title-inner {
    line-height: 20px;
    padding: 6px 0;
    position: relative;
  }
  .header-content {
    font-size: 25px;
    padding-top: 20px;
    position: relative;
    margin: 0 auto;
    width: 100%;
  }


  .float-date{
    font-size: 9px;
    display: flex;
    flex: 1;
  }
  .loading {
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
  }
  .md-spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 50%;
    transform: translate(-50%, -50%);
  }
</style>



